import React, { Component } from 'react'
import Card from './Card';

export default class Favorites extends Component {

    constructor() {
        super()

        this.state = {
            movies: [],
            favIDs: this.getStorage(),
        }
    }


    // Dans un componentDidMount, parcourez le tableau état favIDs et pour chaque élément, lancez la méthode getMovie .
    componentDidMount() {
        this.state.favIDs.map(id => this.getMovie(id))
    }

    // Créez la méthode getStorage qui retourne votre array d’IDs sauvegardés - N'oubliez pas d'utiliser JSON.parse() sur le contenu du local storage pour convertir la chaîne de caractères en tableau avant de le renvoyer
    getStorage = () => {
        return JSON.parse(localStorage.getItem('favorites'))
    }

    // Créez la méthode getMovie qui recevra en paramètre id et qui récupèrera un film à l'adresse suivante : en utilsant fetch
    getMovie = (id) => {
        fetch(`https://api.themoviedb.org/3/movie/${id}?api_key=8c2c5c205a0ac5bb229fe92084e87cf4`)
            .then(res => res.json())
            .then(json =>
                this.setState({
                    movies: [...this.state.movies, json]
                }));
    }



    render() {
        console.log("results", this.state.movies);
        const {favIDs , movies } = this.state
        return (
            <div>
                {favIDs ? (movies.map((movie) => {
                    return (
                        <div  className="d-flex flex-wrap justify-content-around">
                            <Card
                                key={movie.id}
                                film={movie}
                            />
                        </div>
                    )
                })
                ) : (
                    <div >
                        <h2>notFavorite</h2>
                    </div>
                )
                }
            </div>
        )
    }
}
